import React from "react";
import axios from "axios";

class MsgList extends React.Component{
    
    constructor(props){
        super(props);
        this.state = {};
    }

    handleClick = async () => {
        try {    
            const result = await axios.get('https://zipcloud.ibsnet.co.jp/api/search?zipcode=7830060')
            console.log(result);
          } catch (error) {    
            console.log("error!!");
          }
    };

    render(){
        return (
            <>
                <button onClick=
                {this.handleClick}>GET</button>
            </>
        );
    }
}

export default MsgList;